import indexSwiper from "@/components/index/indexSwiper.vue"
import Recommend from "@/components/index/recommend.vue"
import Card from "@/components/common/Card.vue"
import CommodityList from "@/components/common/CommodityList.vue"
import Banner from "@/components/index/Banner.vue"
import Icon from "@/components/index/Icon.vue"
import SuperStar from "@/components/index/SuperStar.vue"
import Hot from "@/components/index/Hot.vue"
import Shop from "@/components/index/Shop.vue"
import $http from "@/common/api/request.js"
export default {
	data() {
		return {
			title: 'Hello',
			// 顶部导航栏内容
			tabBar: [],
			// 顶部导航当前选中索引
			topBarIndex: 0,
			// 顶部导航当前选中索引的id值
			scrollTopIndex: 'top0',
			// 内容块的高度值
			clientHeight: "",
			// 主体内容[推荐商铺，swiper，商品列表]
			mainData: []
		}
	},
	onLoad() {
		this.__init();
	},
	// 获取可视高度
	onReady() {
		uni.getSystemInfo({
			success: (res) => {
				this.clientHeight = res.windowHeight - uni.upx2px(80);
			}
		});
	},
	onShow() {},
	onNavigationBarButtonTap(e) {
		if (e.index === 0) {
			uni.navigateTo({
				url: '../search/search'
			})
		}
	},
	methods: {
		// 请求首页数据
		__init() {
			$http.request({
				url: "/index_list/data"
			}).then((res) => {
				this.tabBar = res.topBar;
				this.mainData = this.initData(res);
			})
		},
		// 添加数据
		initData(res) {
			let arr = [];
			for (let i = 0; i < this.tabBar.length; i++) { // 遍历topBar的长度，swiper的内容数量和topBar数量对应
				let obj = { // 定义一个空对象
					data: [],
					load: "first"
				};
				if (i == 0) { // 
					obj.data = res.data;
				}
				arr.push(obj);
			}
			return arr;
		},
		// 点击顶栏触发
		tabChange(index) {
			if (this.topBarIndex === index) {
				return;
			}
			this.topBarIndex = index;
			this.scrollTopIndex = 'top' + index;

			if (this.mainData[this.topBarIndex].load === "first") {
				this.addData();
			}
		},
		// 对应滑动
		swiperChange(e) { // current 改变时调用
			this.tabChange(e.detail.current)
		},
		// 获取可视区高度 [兼容]
		getClientHeight() {
			let res = uni.getSystemInfoSync();
			// console.log(res)
		},
		// 添加对应topBar的内容
		addData() { //
			let index = this.topBarIndex;
			let id = this.tabBar[index].id;

			let page = Math.ceil(this.mainData[index].data.length / 6 + 1)
			console.log(page)

			$http.request({
				url: `/index_list/${id}/data/${page}`
			}).then((res) => {
				this.mainData[index].data = [...this.mainData[index].data, ...res]
			})

			this.mainData[index].load = "finish"
		},
		// 上拉加载更多
		loadmore() {
			this.addData();
		}
	},
	components: {
		indexSwiper,
		Recommend,
		Card,
		CommodityList,
		Banner,
		Icon,
		SuperStar,
		Hot,
		Shop
	}
}
